html {
  background-color: black;
  -webkit-tap-highlight-color: transparent;
}

html, body, #root {
  height: 100%;
}

.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column-reverse;
      flex-direction: column-reverse;
  height: 100%;
  overflow: hidden;
}

.App > nav {
  background-color: #222;
  height: 80px;
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.UIHierarchy {
  -ms-flex: 1 1;
      flex: 1 1;
}

.UIHierarchy > div > * {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

section.view {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
}

section.view > button {
  font-size: 1em;
  padding: 1em 2em;
  background-color: rgba(0, 0, 0, 0.2);
  border: none;
}

section.view > button:active {
  background-color: rgba(0, 0, 0, 0.4);
}




